import React from "react";
import { ListItem } from "@material-ui/core";

class ChatItem extends React.Component {
  render() {
    const { message, email } = this.props;
    const isOwnMessage = message.author === email;

    return (
      <ListItem style={styles.listItem(isOwnMessage)}>
        <div style={styles.author}>{message.author}</div>
        <div style={styles.container(isOwnMessage)}>
          {message.body}
          <div style={styles.timestamp}>
            {new Date(message.dateCreated.toISOString()).toLocaleString()}
          </div>
        </div>
      </ListItem>
    );
  }
}

const styles = {
  listItem: (isOwnMessage) => ({
    flexDirection: "column",
   
    borderRadius: 15,
    marginTop : 10,
    alignItems: isOwnMessage ? "flex-end" : "flex-start",
  }),
  container: (isOwnMessage) => ({
    maxWidth: "75%",
    borderRadius: isOwnMessage ? '35% 35% 0 35%': '0 35% 35% 35%',
    padding: 16,
    color: isOwnMessage ? "white": "white",
    fontSize: 20,
    background: isOwnMessage ? "linear-gradient(#EB896D, #F75479)" : "linear-gradient(#39cc8f, #39ccb4)",

  }),
  

  author: { fontSize: 10, color: "blue", fontWeight: 900 },
  timestamp: { fontSize: 10, color: "pink", textAlign: "right", paddingTop: 4,fontWeight: 900 },
};

export default ChatItem;